.main_Section4 {
    position: inherit;
    height: 70vh;
    padding: 50px;
    background: var(--primary_color);
}
.main_Section4 .box_content{
    display: flex;
    align-items: center;
    width: 90%;
    height: auto;
    padding: 50px;
    margin: 0 auto;
    background: var(--bg_color);
}
.main_Section4 .box_content .box_img{
    max-width: 40%;
}
.main_Section4 .box_content .box_img img{
    width: 100%;
}
.main_Section4 .box_content .content{
    max-width: 60%;
    padding: 20px;
}
.main_Section4 .box_content .content h2{
    color: var(--primary_color);
    font-size: var(--font_size_subtitles);
    margin-bottom: 10px;
}
.main_Section4 .box_content .content p{
    color: var(--black);
    font-size: var(--font_size_p_small);
    margin-bottom: 40px;
    line-height: 2rem;
}
.main_Section4 .box_content .content .btn_view{
    width: 140px;
    padding: 10px 25px;
    color: var(--black);
    text-decoration: none;
    text-align: center;
    transition: .3s;
    font-size: var(--font_size_p_small);
}
.main_Section4 .box_content .content  .btn_view:hover {
    background-color: var(--btn_hover);
}

@media screen and (max-width: 910px){
    .main_Section4{
        height: auto;
        text-align: center;
        padding: 30px;
    }
    .main_Section4 .box_content{
        flex-direction: column;
        padding: 10px;
    }
    .main_Section4 .box_content .box_img{
        max-width: 80%;
    }
    .main_Section4 .box_content .content{
        max-width: 90%;
    }
}
