body {
    --primary_color: #00bfff;
    --light_blue: #81dfff;
    --btn_hover: #00bfff93;
    --card_hover: #00bfff3d;
    --white: #ffffff;
    --black: #161616;
    --gray_card: #f8f8f8;
    --transparent: #00000000;
    --solid_black: #161616;
    --solid_white: #e0e0e0;
    --gray_border_solid: #c5c5c500;
    --border_radius: 10px;
    --border_color: #0000;
    --filter: invert(0);
    --padding_btn: 12px 35px;
    --gray_txt: #222222;
    --bg_cards_color: #ffffff;
    --bg_color: #f5f5f5;
    --bg_shadown_nav: 6px 9px 40px -10px rgba(151, 151, 151, 0.459);

    --font_size_p: clamp(1rem, 1.8vw, 1.8rem);
    --font_size_p_small: clamp(.9rem, 1.2vw, 1.2rem);
    --font_size_btn: clamp(.8rem, 1vw, 1rem);
    --font_size_titles: clamp(3rem, 5vw, 5rem);
    --font_size_subtitles: clamp(1.8rem, 2.8vw, 2.8rem);
}

@media (prefers-color-scheme: dark) {
    body {
        --primary_color: #00B1ED;
        --light_blue: #81dfff;
        --white: #161616;
        --black: #f1f1f1;
        --gray_card: #1A1A1A;
        --solid_black: #161616;
        --solid_white: #e0e0e0;
        --gray_border_solid: #1A1A1A;
        --filter: invert(100%);
        --gray_txt: #222222;
        --bg_cards_color: #1d1d1d;
        --card_hover: #00384b36;
        --border_color: #00bfff;
        --bg_color: #161616;
        --bg_shadown_nav: 6px 9px 40px -10px rgba(151, 151, 151, 0);
        --btn_hover: #00384b;
    }

}

.gradient_buttons {
    border: 1px solid transparent;
    border-image: linear-gradient(90deg, rgba(0, 177, 237, 1) 0%, rgba(0, 177, 237, 0.13209033613445376) 100%);
    border-image-slice: 1;
}
.gradient_buttons_invert{
    border: 1px solid transparent;
    border-image: linear-gradient(90deg, rgba(0, 177, 237, 0) 0%, rgba(0, 177, 237, 1) 100%);
    border-image-slice: 1;
}
.box_shadow{
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}