.mainSection3{
    position: relative;
    background: var(--bg_color);
    height: 90vh;
    width: 100%;
}
.mainSection3 .box_Section3{
    width: 100%;
    height: 100%;
    padding: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.mainSection3 .box_Section3 .title{
    font-size: var(--font_size_subtitles);
    color: var(--primary_color);
}
.mainSection3 .box_Section3 .subtitle{
    font-size: var(--font_size_p);
    color: var(--black);
}
.mainSection3 .box_Section3 .cards{
    padding: 50px;
    display: flex;
    justify-content: center;
}
.mainSection3 .box_Section3 .cards .card_content{
    width: 25%;
    padding: 40px;
    margin-right: 40px;
    background: var(--bg_cards_color);
    transition: .5s;
}
.mainSection3 .box_Section3 .cards .card_content:hover{
    background: var(--card_hover);
    transform: scale(1.1);
}
.mainSection3 .box_Section3 .cards .card_content svg{
    color: var(--black);
    font-size: var(--font_size_subtitles);
    margin-bottom: 10px;
}
.mainSection3 .box_Section3 .cards .card_content h2{
    color: var(--primary_color);
    font-size: var(--font_size_p);
    margin-bottom: 20px;
}
.mainSection3 .box_Section3 .cards .card_content p{
    color: var(--black);
    font-size: var(--font_size_p_small);
    margin-bottom: 30px;
    font-weight: 200;
}
.mainSection3 .box_Section3 .cards .card_content a{
    
    color: var(--primary_color);
    text-decoration: none;
    transition: .3s;
}

@media screen and (max-width: 910px){
    .mainSection3{
        height: auto;
    }
    .mainSection3 .box_Section3 .title,
    .mainSection3 .box_Section3 .subtitle{
        text-align: center;
    }
    .mainSection3 .box_Section3 .cards{
        align-items: center;
        flex-direction: column;
        padding: 50px 0;
    }
    .mainSection3 .box_Section3 .cards .card_content{
        margin-bottom: 15px;
        margin-right: 0px;
        width: 100%;
        max-width: 95%;
    }
}