.main {
  position: relative;
  height: auto;
  width: 100%;
  background-color: var(--bg_color);
  z-index: 0;
  padding: 50px;
}

.img_bg img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10; 
  opacity: 30%;
} 


.box_header{
  width: 90%;
  margin: 8% auto;
  display: flex;
  align-items: center;
  justify-content: space-between;

}
.image_header img{
  width: 100%;
  animation: moveUpDown 4s ease-in-out infinite;
}
.header_content {
  max-width: 50%;
  opacity: 0; 
  animation: fadeIn 1s forwards; 
}

.slogan {
  font-size:var(--font_size_p);
  font-weight: 500;
  color: var(--black);
}
.title {
  font-size: clamp(3rem, 5vw, 5rem);
  color: var(--primary_color);
  font-weight: 800;
  letter-spacing: -1px;
}
.title span {
  color: var(--light_blue);
  
}

.phrase{
  margin-bottom: 40px;
  font-size:  var(--font_size_p);
  color: var(--black);
  font-weight: 300;
}

.btn_services {
  padding: var(--padding_btn);
  color: var(--black);
  background: var(--primary_color);
  text-decoration: none;
  transition: .3s;
  font-size: var(--font_size_btn);
  min-width: 100%;
}

.btn_services:hover {
  background-color: var(--btn_hover);
  color: var(--black);
}

.arrow_down {
  margin-top: 30px;
  color: var(--black);
  animation: moveUpDown 2s ease-in-out infinite;
}

@media screen and (max-width: 910px) {
  .main{
    padding: 50px 10px;
  }
  .box_header{
    width: 90%;
    margin: 12% auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
  }
  .header_content{
    max-width: 100%;
  }
  .image_header img{
    width: 100%;
    margin-top: 50px;
  }

}
