.main_section1 {
    position: inherit;
    padding: 100px;
    height: auto;
    background-color: var(--bg_color);
}

.txt_content {
    text-align: center;
    margin: 0 auto;
}

.txt_content h2 {
    color: var(--primary_color);
    font-size: var(--font_size_titles);
}

.txt_content p {
    color: var(--black);
    margin-top: 20px;
    margin-bottom: 50px;
    font-weight: 300;
}

.cards {
    max-width: 1400px;
    height: auto;
    margin: 0 auto;
    z-index: 10;
}

.cards .card_1 {
    display: flex;
    padding: 30px;
    background: var(--bg_cards_color);
    border-radius: var(--border_radius);
    transition: .5s;
}
.cards .card_1:hover{
    background: var(--card_hover);

}
.cards .card_1 .img_box{
    max-width: 40%;
}
.cards .card_1 .img_box img {
    width: 100%;
}

.cards .card_1 .content_card_1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cards .card_1 .content_card_1 .title {
    height: auto;
    display: flex;
    align-items: center;
}

.cards .card_1 .content_card_1 .title svg {
    font-size: var(--font_size_subtitles);
    margin-right: 20px;
    color: var(--primary_color);
}

.cards .card_1 .content_card_1 .title h2 {
    font-size: var(--font_size_subtitles);
    color: var(--primary_color);
}

.cards .card_1 .content_card_1 h3 {
    font-weight: 500;
    font-size: var(--font_size_p_small);
    margin-bottom: 20px;
    color: var(--primary_color);
    margin-top: 20px;
}

.cards .card_1 .content_card_1 p {
    font-weight: 300;
    color: var(--black);
    font-size: var(--font_size_p_small);
}

.cards .card_1 .content_card_1 .btn_view {
    width: 140px;
    margin-top: 50px;
    padding: 10px 25px;
    color: var(--black);
    text-decoration: none;
    text-align: center;
    transition: .3s;
}

.cards .card_1 .content_card_1 .btn_view:hover {
    background-color: var(--btn_hover);
}


.card_small {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;

}

.cards .card_2 {
    display: flex;
    padding: 30px;
    width: 49%;
    border-radius: 30px 0 30px 0;
    background: var(--bg_cards_color);
    transition: .5s;
}
.cards .card_2:hover{
    background: var(--card_hover);

}
.cards .card_2 .content_card_2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.cards .card_2 .content_card_2 .title {
    height: auto;
    display: flex;
    align-items: center;
}

.cards .card_2 .content_card_2 .title svg {
    font-size: var(--font_size_subtitles);
    margin-right: 20px;
}

.cards .card_2 .content_card_2 .title h2 {
    color: var(--primary_color);
    font-size: var(--font_size_subtitles);
}

.cards .card_2 .content_card_2 h3 {
    font-weight: 500;
    font-size: var(--font_size_p_small);
    color: var(--black);
    margin-bottom: 20px;
    margin-top: 20px;
}

.cards .card_2 .content_card_2 p {
    font-weight: 300;
    color: var(--black);
    font-size: var(--font_size_p_small);
}

.cards .card_2 .content_card_2 .btn_view {
    width: 140px;
    margin-top: 50px;
    padding: 10px 25px;
    color: var(--black);
    text-decoration: none;
    text-align: center;
    transition: .3s;
}

.cards .card_2 .content_card_2 .btn_view:hover {
    background-color: var(--btn_hover);
}

@keyframes show {
    from {
        opacity: 0;
        scale: 25%;
    }

    to {
        opacity: 1;
        scale: 100%;
    }
}

.cards .card_1,
.cards .card_2 {
    view-timeline-name: --image;
    view-timeline-axis: block;

    animation-timeline: --image;
    animation-name: show;

    animation-range: entry 0 cover 30%;
    animation-fill-mode: both;
}

@media screen and (max-width: 910px){
    .main_section1{
        padding: 40px;
    }
    .cards{
        text-align: center;
    }
    .cards .card_1{
        justify-content: center;
        flex-direction: column;
        align-items: center;
        padding: 40px;
    }
    .cards .card_1 img{
        display: none;
    }
    .cards .card_1 .title{
        justify-content: center;
        flex-direction: column;
    }
    .cards .card_1 .content_card_1 .title svg{
        margin-right: 0;
        margin-bottom: 10px;
    }
    .cards .card_1 .btn_view{
        margin: 0 auto;
    }
    .cards .card_small{
        flex-direction: column;
        margin-top: 0;
    }
    .cards .card_small .card_2{
        width: 100%;
        padding: 40px;
        margin-top: 30px;
        border-radius: var(--border_radius);
    }
    .cards .card_small .card_2 .title{
        justify-content: center;
        flex-direction: column;
    }
    .cards .card_2 .content_card_2 .title svg{
        margin-right: 0;
        margin-bottom: 10px;
    }
    .cards .card_2 .btn_view{
        margin: 0 auto;
    }

}