.main_section2 {
    position: inherit;
    height: 70vh;
    background-color: var( --bg_cards_color);
}
.main_section2 .box_section2{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.main_section2 .box_section2 img{
    background-size: cover;
    opacity: 90% ;
    z-index: 10;
    width: 50%;
    height: 100%;
}
.box_section2 .txt_container{
    width: 50%;
    padding: 50px;
}
.box_section2 .txt_container h2{
    font-size: var(--font_size_subtitles);
    color: var(--primary_color);
}
.box_section2 .txt_container h3{
    font-size: var(--font_size_p);
    margin-top: 15px;
    color: var(--black);
}
.box_section2 .txt_container h4{
    font-size: var(--font_size_p);
    margin-top: 15px;
    color: var(--primary_color);
}
.box_section2 .txt_container p{
    font-size: var(--font_size_p_small);

    margin-top: 20px;
    font-weight: 200;
    color: var(--black);
}

@media screen and (max-width: 910px){
    .main_section2 {
        height: auto;
    }
    .main_section2 .box_section2{
        flex-direction: column;
        text-align: center;
    }
    .main_section2 .box_section2 img{
        width: 100%;
    }
    .box_section2 .txt_container{
        width: 100%;
        padding: 30px;
    }
}