.mainNumberSection{
    position: relative;
    height: 100vh;
    width: 100%;
    background: var(--bg_color);
    color: var(--black);
}
.mainNumberSection .content{
    display: flex;
    align-items: center;
    flex-direction: column;
    }
.mainNumberSection .content span .icon{
    margin-top: 70px;
    font-size: 4rem;
    animation: moveUpDown 4s ease-in-out infinite;
}
.mainNumberSection .content h3{
    width: 50%;
    margin-top: 30px;
    font-size: var(--font_size_subtitles);
    font-weight: 700;
    letter-spacing: -2px;
    text-align: center;
}
.mainNumberSection .content .box{
    margin-top: 50px;
    display: flex;
    text-align: center;
    justify-content: space-between;
    max-width: 1000px;
    width: 70%;
    
}
.mainNumberSection .content .box .box_content{
    width: 48%;
    padding: 10px;
    border-top: 2px solid var(--black);
  
}
.mainNumberSection .content .box .box_content h2{
    font-size: var(--font_size_titles);
}
.mainNumberSection .content .box .box_content p{
    font-size: clamp(1rem 1.8vw 1.8rem);
    font-weight: 300;
}
@keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-20px); /* Animaci√≥n de desplazamiento hacia abajo */
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  path{
    fill: var(--primary_color);
  }
  .wave{
    background-color: var(--bg_color);
  }
  
  @media screen and (max-width: 910px){
    .mainNumberSection{
        height: auto;
    }
    .mainNumberSection .content h3{
        margin-bottom: 40px;
        
    }
    .mainNumberSection .content .box {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        margin-top: 10px;
    }
    .mainNumberSection .content .box .box_content {
        width: 95%;
    }
  }